import Vue from 'vue'
import axios from 'axios'


axios.defaults.baseURL = 'http://lfl2.api.llasqq.com/';
// axios.defaults.baseURL = 'http://120.76.236.101:8082/';
// axios.defaults.baseURL = 'http://192.168.1.38:8082/';
axios.defaults.headers.post['Content-Type'] = 'application/json';

// 
axios.interceptors.request.use(
  config => {
    if(config.url.indexOf('get/detail') == -1){
    	config.headers.common['version'] = '2';
    }
	return config;
  }
  
);

